<template>
  <div id="app" class="mx-5 px-5">
    <b-navbar class="py-5">
      <b-nav-form>
        <b-form-input
          placeholder="Search"
          size="sm"
          v-model="query"
          v-on:change="getGames"
        ></b-form-input>
        <b-button
          type="submit"
          variant="secondary"
          size="sm"
          class="ml-3"
        >Submit</b-button>
      </b-nav-form>
    </b-navbar>
    <wrapper
      v-bind:games="games"
      v-bind:colors="colors"
      v-bind:categories="categories"
    ></wrapper>
  </div>
</template>

<script>
import axios from 'axios'
import Wrapper from './components/Wrapper';
import { API_HOST } from './config.js';

export default {
  name: 'app',
  components: {
    Wrapper
  },
  data: function () {
    return {
      query: '',
      games: [],
      colors: [],
      categories: []
    }
  },
  created: function () {
    this.getColors();
    this.getCategories();
  },
  methods: {
    getGames: function () {
      axios.get(`${API_HOST}/games?name=${this.query}`)
      .then(result => {
        console.log('result.data', result.data);
        this.games = result.data;
      });
    },
    getColors: function () {
      axios.get(`${API_HOST}/colors`)
      .then(result => this.colors = result.data);
    },
    getCategories: function () {
      axios.get(`${API_HOST}/categories`)
      .then(result => this.categories = result.data);
    }
  },
  watch: {
    query: function () {
      this.getGames();
    }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400,500,700');
#app {
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app * {
  font-size: 12px;
}
</style>



